export default "import EmblaCarousel from 'embla-carousel';\nimport { setupProgressBar } from './EmblaCarouselProgressBar';\nimport { addPrevNextBtnsClickHandlers } from '../EmblaCarouselArrowButtons';\nimport '../css/base.css';\nimport '../css/sandbox.css';\nimport '../css/embla.css';\n\nconst OPTIONS = {};\n\nconst emblaNode = document.querySelector('.embla');\nconst viewportNode = emblaNode.querySelector('.embla__viewport');\nconst prevBtn = emblaNode.querySelector('.embla__button--prev');\nconst nextBtn = emblaNode.querySelector('.embla__button--next');\nconst progressNode = (emblaNode.querySelector('.embla__progress__bar'));\n\nconst emblaApi = EmblaCarousel(viewportNode, OPTIONS);\nconst { applyProgress, removeProgress } = setupProgressBar(emblaApi, progressNode);\n\nconst removePrevNextBtnsClickHandlers = addPrevNextBtnsClickHandlers(emblaApi, prevBtn, nextBtn);\n\nemblaApi\n    .on('init', applyProgress)\n    .on('reInit', applyProgress)\n    .on('scroll', applyProgress)\n    .on('slideFocus', applyProgress)\n    .on('destroy', removeProgress)\n    .on('destroy', removePrevNextBtnsClickHandlers);\n";